"use client";

import { useState, useEffect, Suspense } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { useRouter, useSearchParams } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { authApi } from "../lib/api";
import AnimatedBackground from "../components/AnimatedBackground";
import { Eye, EyeOff, ArrowRight, AlertCircle } from "lucide-react";

function LoginForm() {
    const router = useRouter();
    const params = useSearchParams();
    const { login, isAuthenticated } = useAuthStore();
    const [form, setForm] = useState({ email: "", password: "" });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const [showPw, setShowPw] = useState(false);

    useEffect(() => {
        if (isAuthenticated) router.push("/dashboard");
    }, [isAuthenticated, router]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError("");
        setLoading(true);
        try {
            const { data } = await authApi.login(form);
            login(data.access_token, data.user);
            router.push("/dashboard");
        } catch (err: any) {
            setError(err.response?.data?.detail || "Invalid credentials. Try again.");
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="relative min-h-screen flex">
            <AnimatedBackground />

            {/* Left brand panel */}
            <div className="hidden lg:flex lg:w-1/2 bg-[#0d0d0d] flex-col items-start justify-center p-16 relative z-10">
                <div className="flex items-center gap-2 mb-16">
                    <div className="flex gap-1">
                        <div className="w-3 h-3 bg-[#f97316] rounded-sm" />
                        <div className="w-3 h-3 bg-white/30 rounded-sm" />
                    </div>
                    <span className="font-serif font-bold text-xl text-white">Perspectra.</span>
                </div>
                <motion.div
                    initial={{ opacity: 0, y: 40 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8 }}
                >
                    <h2 className="font-serif font-black text-5xl text-white leading-tight mb-6">
                        Welcome<br />
                        <em className="gradient-text not-italic">back.</em>
                    </h2>
                    <p className="text-white/60 text-lg leading-relaxed max-w-sm">
                        Your cognitive profile remembers everything. Continue from where your thinking left off.
                    </p>
                </motion.div>
                <div className="mt-auto text-white/30 text-xs tracking-widest uppercase">
                    Think // Learn // Master
                </div>
            </div>

            {/* Right form */}
            <div className="w-full lg:w-1/2 flex items-center justify-center px-6 py-24 relative z-10">
                <motion.div
                    className="w-full max-w-md"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <div className="lg:hidden flex items-center gap-2 mb-10">
                        <div className="flex gap-1">
                            <div className="w-2.5 h-2.5 bg-[#f97316] rounded-sm" />
                            <div className="w-2.5 h-2.5 bg-[#0d0d0d] rounded-sm" />
                        </div>
                        <span className="font-serif font-bold text-lg">Perspectra.</span>
                    </div>

                    <h1 className="font-serif font-bold text-3xl text-[#0d0d0d] mb-2">Sign in</h1>
                    <p className="text-[#6b7280] text-sm mb-8">
                        Don&apos;t have an account?{" "}
                        <Link href="/register" className="text-[#f97316] font-medium no-underline hover:underline">
                            Create one →
                        </Link>
                    </p>

                    {error && (
                        <motion.div
                            className="flex items-center gap-2 bg-red-50 border border-red-200 text-red-600 rounded-md px-4 py-3 text-sm mb-6"
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                        >
                            <AlertCircle className="w-4 h-4 shrink-0" /> {error}
                        </motion.div>
                    )}

                    <form onSubmit={handleSubmit} className="flex flex-col gap-4">
                        <div>
                            <label className="block text-sm font-medium text-[#374151] mb-1.5">Email</label>
                            <input
                                type="email"
                                required
                                value={form.email}
                                onChange={(e) => setForm({ ...form, email: e.target.value })}
                                placeholder="your@email.com"
                                className="w-full border border-[#e5e7eb] rounded-sm px-4 py-3 text-sm bg-white outline-none focus:border-[#f97316] transition-colors"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-[#374151] mb-1.5">Password</label>
                            <div className="relative">
                                <input
                                    type={showPw ? "text" : "password"}
                                    required
                                    value={form.password}
                                    onChange={(e) => setForm({ ...form, password: e.target.value })}
                                    placeholder="••••••••"
                                    className="w-full border border-[#e5e7eb] rounded-sm px-4 py-3 pr-12 text-sm bg-white outline-none focus:border-[#f97316] transition-colors"
                                />
                                <button
                                    type="button"
                                    onClick={() => setShowPw(!showPw)}
                                    className="absolute right-3 top-1/2 -translate-y-1/2 text-[#9ca3af] hover:text-[#6b7280] transition-colors"
                                >
                                    {showPw ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                                </button>
                            </div>
                        </div>

                        <button type="submit" disabled={loading} className="btn-accent w-full mt-2 py-3 flex items-center justify-center gap-2">
                            {loading ? (
                                <span className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                            ) : (
                                <>Sign In <ArrowRight className="w-4 h-4" /></>
                            )}
                        </button>
                    </form>
                </motion.div>
            </div>
        </div>
    );
}

export default function LoginPage() {
    return (
        <Suspense>
            <LoginForm />
        </Suspense>
    );
}
