"use client";

import { useEffect, useRef } from "react";

export default function AnimatedBackground() {
    const svgRef = useRef<SVGSVGElement>(null);

    // Animate dots on canvas with requestAnimationFrame
    useEffect(() => {
        const svg = svgRef.current;
        if (!svg) return;

        const W = window.innerWidth;
        const H = window.innerHeight;
        const NODES = 42;
        const MAX_DIST = 180;

        // Create random nodes
        type Node = { x: number; y: number; vx: number; vy: number };
        const nodes: Node[] = Array.from({ length: NODES }, () => ({
            x: Math.random() * W,
            y: Math.random() * H,
            vx: (Math.random() - 0.5) * 0.4,
            vy: (Math.random() - 0.5) * 0.4,
        }));

        // Build SVG elements
        svg.setAttribute("viewBox", `0 0 ${W} ${H}`);

        const dots: SVGCircleElement[] = nodes.map(() => {
            const c = document.createElementNS("http://www.w3.org/2000/svg", "circle");
            c.setAttribute("r", "2");
            c.setAttribute("fill", "#6366f1");
            svg.appendChild(c);
            return c;
        });

        const lines: SVGLineElement[] = [];
        for (let i = 0; i < NODES; i++) {
            for (let j = i + 1; j < NODES; j++) {
                const l = document.createElementNS("http://www.w3.org/2000/svg", "line");
                l.setAttribute("stroke", "#6366f1");
                l.setAttribute("stroke-width", "0.8");
                svg.insertBefore(l, dots[0]);
                lines.push(l);
            }
        }

        let animId: number;
        const tick = () => {
            nodes.forEach((n) => {
                n.x += n.vx;
                n.y += n.vy;
                if (n.x < 0 || n.x > W) n.vx *= -1;
                if (n.y < 0 || n.y > H) n.vy *= -1;
            });

            dots.forEach((d, i) => {
                d.setAttribute("cx", String(nodes[i].x));
                d.setAttribute("cy", String(nodes[i].y));
            });

            let li = 0;
            for (let i = 0; i < NODES; i++) {
                for (let j = i + 1; j < NODES; j++) {
                    const dx = nodes[i].x - nodes[j].x;
                    const dy = nodes[i].y - nodes[j].y;
                    const dist = Math.sqrt(dx * dx + dy * dy);
                    if (dist < MAX_DIST) {
                        const op = 1 - dist / MAX_DIST;
                        lines[li].setAttribute("x1", String(nodes[i].x));
                        lines[li].setAttribute("y1", String(nodes[i].y));
                        lines[li].setAttribute("x2", String(nodes[j].x));
                        lines[li].setAttribute("y2", String(nodes[j].y));
                        lines[li].setAttribute("stroke-opacity", String(op * 0.5));
                        lines[li].setAttribute("display", "");
                    } else {
                        lines[li].setAttribute("display", "none");
                    }
                    li++;
                }
            }

            animId = requestAnimationFrame(tick);
        };

        animId = requestAnimationFrame(tick);
        return () => cancelAnimationFrame(animId);
    }, []);

    return (
        <div className="mesh-bg">
            <svg ref={svgRef} />
        </div>
    );
}
